import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import * as $ from 'jquery';
import { HomePage } from '../home/home';
@Component({
  selector: 'page-item-data',
  templateUrl: 'item-data.html',
})
export class ItemDataPage {
  item:any;
  id:any;
  itemName:any;
  token:any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage) {
    this.id = this.navParams.get('param1');
    this.item = this.navParams.get('param2');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ItemDataPage');
  }

  AddNewItem(){
    console.log("AddNewItem");
    var storage = this.storage;
    var navCtrl = this.navCtrl;

    this.storage.get('token').then((data)=>{
      this.token = data;
    });

    let elem = this.item;
    let itemName = this.itemName;
    let id = this.id;
    let value;
    let unit;

    var settings = {
      "async": true,
      "crossDomain": true,
      "url": `http://mw3demo.cumulocity.com/measurement/measurements?source=${this.id}&type=${this.item}`,
      "method": "GET",
      "headers": {
        "authorization": `Basic ${this.token}`,
        "cache-control": "no-cache",
      }
    }


    $.ajax(settings).done(function (response) {
      var obj = response.measurements.elem;
      if(elem == "c8y_TemperatureMeasurement"){
        value = obj.T.value;
        unit = obj.T.unit;
      }else if(elem == "c8y_LightMeasurement"){
        value = obj.e.value;
        unit = obj.e.unit;
      }else if(elem == "c8y_AccelerationMeasurement"){
        value = obj.acceleration.value;
        unit = obj.acceleration.unit;
      }

      var newItem = {
        "deviceID":id,
        "name":itemName,
        "item":elem,
        "value":value,
        "unit":unit
      }

      // storage.get('userItems').then((data)=>{
      //   if(data == null){
      //     storage.set('userItems', newItem);
      //   }else{
      //     data.push(newItem);
      //     storage.set("userItems", data)
      //   }
      // })

      navCtrl.push(HomePage);
    });

  }

}



