import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { DeviceDataPage } from '../device-data/device-data';
import { Storage } from '@ionic/storage';


@Component({
  selector: 'page-devices',
  templateUrl: 'devices.html',
})
export class DevicesPage {

  devices:any;

  constructor(public navCtrl: NavController, public storage: Storage, public navParams: NavParams) {

    // this.devices = [{"id":"23235", "name":"device1", "c8y_SupportedMeasurements":["c8y_LightMeasurement", "c8y_AccelerationMeasurement"]},
    //                 {"id":"1111", "name":"device2", "c8y_SupportedMeasurements":["c8y_TemperatureMeasurement", "c8y_LightMeasurement"]}]

    this.storage.get('devices').then((data) => {
      this.devices = data;
      console.log("devices from get",this.devices)
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  deviceDate(device){
    this.navCtrl.push(DeviceDataPage,{'param1': device});
  }

}



