import { Component } from '@angular/core';
import { NavController, ViewController , NavParams, reorderArray} from 'ionic-angular';
import { Storage } from '@ionic/storage';
import {DataServiceProvider} from '../../providers/data-service/data-service';
import { DevicesPage } from '../devices/devices';
import { Http } from '@angular/http';
import * as $ from 'jquery';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  btnName:any = "edit";
  items:any[];
  flag: any = false;
  devices:any;
  username:string;
  token:string;
  icons:any = {"c8y_TemperatureMeasurement": "ios-thermometer", "c8y_LightMeasurement":"md-bulb", "c8y_AccelerationMeasurement":"md-compass"};


    constructor(public navCtrl: NavController,public navParams: NavParams,
              private viewCtrl: ViewController,public storage: Storage,
              public dataService : DataServiceProvider, public http: Http) {

    // this.doRefresh(0);
  }

  diplayItems(){
    this.storage.get('userItems').then((data)=>{
      if(data != null){
        this.items = data;
      }
    })
  }

  doRefresh(refresher){
      this.storage.get('devices').then((data) => {
        this.items = data;
        if(refresher != 0)
           refresher.complete();
      });
  };

  reorderItems(indexes){
    this.items = reorderArray(this.items, indexes);
  };


  ionViewWillEnter() {
      this.viewCtrl.showBackButton(false);
  }

  getData(){

    var storage = this.storage;
    var navCtrl = this.navCtrl;
    var devices = this.devices;

    function myFilter(objs){
      return objs.filter((obj)=>{
        return obj['c8y_SupportedMeasurements'];
      }).map((obj)=>{
        return (({ id, name, c8y_SupportedMeasurements }) => ({ id, name, c8y_SupportedMeasurements }))(obj)
      })
    }

    this.storage.get('userData.username').then((data)=>{
      this.username = data;
    });
    this.storage.get('userData.token').then((data)=>{
      this.token = data;
    });

    var settings = {
      "async": true,
      "crossDomain": true,
      "url": `http://mw3demo.cumulocity.com/inventory/managedObjects?owner=${this.username}`,
      // "url": "http://mw3demo.cumulocity.com/inventory/managedObjects?owner=ahmed.essam@mw3.com.eg",
      "method": "GET",
      "headers": {
        // "authorization":"Basic YWhtZWQuZXNzYW1AbXczLmNvbS5lZzpTYW1sZWUxMiFA",
        "authorization": `Basic ${this.token}`,
        "cache-control": "no-cache",
        "postman-token": "18e9de96-efcd-b4f4-646e-e0b3d99d8cf8",
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Headers": "*",
      }
    }


    $.ajax(settings).done(function (response) {
      var objs = response.managedObjects;
      var newObjs = myFilter(objs);
      var devices = newObjs;
      storage.set('devices', devices);
      navCtrl.push(DevicesPage);
    });

  }

  showDevices(){
    this.navCtrl.push(DevicesPage);
    // this.getData()
  }

}



